import React, { useState } from 'react'
import { useForm } from "react-hook-form";
import axios from 'axios'


function Contact({ data }) {
   const [res, setRes] = useState('')
   const { handleSubmit, register, errors } = useForm();
   const onSubmit = (values, e) => {
      axios.post("https://check-app-deploy.herokuapp.com/access1", values).then((res) => {
         console.log(res.data)
         setRes(res.data)
         setTimeout(() => {
            setRes('')
            e.target.reset()

         }, 3000)
      })
   }


   return (

      <section id="contact">
         <div className="row section-head">

            <div className="two columns header-col">

               <h1><span>Get In Touch.</span></h1>

            </div>

            <div className="ten columns">

               <p className="lead">{data?.message}</p>

            </div>

         </div>

         <div className="row">
            <div className="eight columns">

               <form onSubmit={handleSubmit(onSubmit)}>
                  <label htmlFor="contactName">Name <span className="required">*</span></label>
                  <input
                     name="username"
                     ref={register({
                        required: "UserName Required!!!",
                     })}
                  />




                  <label htmlFor="contactEmail">Email <span className="required">*</span></label>
                  <input
                     name="email"
                     ref={register({
                        required: "Email required!!!",
                        pattern: {
                           value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                           message: "invalid emailid"
                        }
                     })}
                  />

                  <label htmlFor="contactSubject">Subject</label>
                  <input
                     name="subject"
                     ref={register}
                  />
                  <label htmlFor="contactSubject">Message</label>
                  <input
                     name="message"
                     ref={register}
                  />



                  <div>
                     <button type="submit" className="submit">Submit</button>
                     <span id="image-loader">
                        <img alt="" src="images/loader.gif" />
                     </span>
                  </div>
               </form>

               <div id="message-warning"> Error boy</div>
               <div id="message-success">
                  <i className="fa fa-check"></i>Your message was sent, thank you!<br />
               </div>
            </div>


            <aside className="four columns footer-widgets">
               <div className="widget widget_contact">

                  <h4>Address and Phone</h4>
                  <p className="address">
                     {data?.name}<br />
                     {data?.address.city},{data?.address.state}<br />
                     <span>{data?.phone}</span>
                  </p>
               </div>

               <div className="widget widget_tweets">

               </div>
               <br />
               <br />
               {res.length > 0 ? <p style={{ color: "white", border: "1px soloid white" }}>{res}</p> : null}
               <p style={{ color: "white" }}>
                  {errors.username && errors.username.message}
               </p>
               <p style={{ color: "white" }}>
                  {errors.email && errors.email.message}
               </p>

            </aside>
         </div>

      </section>
   )
}

export default Contact


